.wellness {
  display: flex;
  width: 70%;
  margin: auto;
  justify-content: center;
  // margin-top: 30px;
  margin-bottom: 40px;
  color: rgba(9, 28, 63, 0.5);
  padding-bottom: 42px;
  margin-top: 30px;
  /* shld remove ltr */
}

.wellnessdiv {
  width: 200px;
  height: 230px;
  margin-top: auto;
  margin-bottom: auto;
  background-color: #1987FB;

}

.divBackWellness {
  border: 1px solid transparent;
  background-image: url('https://res.cloudinary.com/bits-pilani/image/upload/fl_lossy,f_auto,q_auto/v1596260409/New-home-screen/Ellipse_162_wsxknf.svg');
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-size: 550px;
}

.getstarted {
  box-shadow: 0px 10px 26px rgba(94, 125, 182, 0.1);
  width: 350px;
  background-color: #fff;
  padding: 40px 10px 35px 10px;
}

.planprice {
  height: 90px;
  color: white;
  font-size: 22px;
  text-align: center;
  font-weight: 600;
}

.innerdiv {
  font-family: Gilroy;
  width: 140px;
  margin: auto;
  margin-top: 30px;
}

.rupees-well-cls {
  font-family: Roboto;
  color: white;
  font-size: 24px;
  text-align: center;
  font-weight: 600;
  letter-spacing: 1px;
  margin-top: 0px;
}

.permonth {
  font-size: 8px;
  margin-top: 8px;
}

.checkboxflex {
  display: flex;
}


.checkcontent {
  display: flex;
  margin-top: 20px;
  width: 90%;
  margin-left: 20px;
}

.checkk {
  width: 50px;
  padding-top: 15px;
  padding-bottom: auto;
  margin-right: 5px;
}

.content {
  font-family: Roboto;
  width: fit-content;
  margin-left: 15px;

}

.tick-img {
  height: 30px;
}

.tick-mem-cls {
  padding-top: 17px;
}

.getbtn {
  border: none;
  outline: none !important;
  width: 150px;
  border-radius: 25px;
  padding: 10px;
  background-color: #007DFF;
  color: white;
  margin-top: 35px;
  margin-bottom: 20px;
  cursor: pointer;

}

.getbtn:hover {
  background-color: #1B6FC9;
}

.mobview {
  display: none;
}

/*Arun changes*/
.wel_fnt_chg_k {
  font-weight: 800;
  font-size: 23px;
  //  line-height: 40px;
  letter-spacing: 0.006em;
  width: 190px;
  //  margin: 30px auto auto;
}

.well_amtCls {
  font-weight: 800;
  font-family: Gilroy;
  font-size: 30px;
  padding-top: 20px;
  letter-spacing: 0.006em;
}

.well_dutnCls {
  font-size: 10px;
  line-height: 14px;
}

.well-line-webview {
  display: block !important;
}

.well-line-mobview {
  display: none !important;
}

@media(max-width: 720px) {
  .wellness {
    flex-direction: column;
    width: 100%;
    margin: auto;

  }

  .divBackWellness {
    border: 1px solid transparent;
    background-image: none;
  }

  .wellnessdiv {
    width: 200px;
    height: 230px;
    margin: auto;
  }

  .getstarted {
    display: none;
  }

  .mobview {
    display: block;
    margin: auto;
  }

  .wellnessdiv {
    background-color: #F5F9FF;

  }

  .planprice {
    height: 80px;
    color: #091C3F;
  }

  .rupee {
    padding-top: 0px;
    color: #091C3F;
    font-size: 28px;
    font-weight: bold;
    //  border-top: 1px dotted #091C3F;
    margin-top: 20px;
  }

  .permonth {
    color: #091C3F;
    font-size: 8px;
  }

  .wel_fnt_chg_k {
    font-size: 22px;
    line-height: 124%;
    letter-spacing: 0.005em;
  }

  .well_amtCls {
    color: #091C3F;
    line-height: 124%;
    letter-spacing: 0.005em;
  }

  .well-line-webview {
    display: none;
  }

  .well-line-mobview {
    display: block !important;

  }

}